import { useStaticQuery, graphql } from 'gatsby'

export const useEquipmentQuery = () => {
  const data = useStaticQuery(graphql`
    query EquipmentQuery {
      allEquipmentJson {
        nodes {
          type
          img
          alt
          id
        }
      }
    }
  `)
  
  return data?.allEquipmentJson?.nodes || []
}
