import React, { Children, cloneElement, useState, useRef } from 'react'
import PropTypes from 'prop-types'
import { useTheme } from 'emotion-theming'
import useComponentSize from '@rehooks/component-size'
import { useMediaQuery } from 'react-responsive'

import { screenReaderOnly } from '../../styles/shared'
import { IconButton } from '../IconButton'
import { ReactComponent as Left } from '../../svg/left.svg'
import { ReactComponent as Right } from '../../svg/right.svg'

import composeStyles from './styles'

const computeTranslate = (rootWidth, totalCardWidth) => {
  if (!rootWidth || rootWidth === 0) {
    return 0
  }

  return (rootWidth - totalCardWidth) / 2
}

const ReviewSlider = ({ children }) => {
  const theme = useTheme()
  const styles = composeStyles(theme)

  const rootRef = useRef(null)
  const { width: rootWidth } = useComponentSize(rootRef)

  const isPhone = useMediaQuery({ query: `(${theme.breakpoints.phone})`})

  const cardWidth = isPhone ? 232 : 440
  const cardMargin = 12
  const totalCardWidth = cardWidth + (cardMargin * 2)

  const childrenArray = Children.toArray(children)
  
  const [currentReviewIndex, setCurrentReview] = useState(Math.floor(childrenArray.length / 2))

  // TODO: Reduce duplication between these funcs
  const handleNextReview = () => {
    setCurrentReview(currentReviewIndex => {
      const nextReviewIndex = currentReviewIndex + 1
      return nextReviewIndex > childrenArray.length - 1 ? currentReviewIndex : nextReviewIndex
    })
  }

  const handlePreviousReview = () => {
    setCurrentReview(currentReviewIndex => {
      const nextReviewIndex = currentReviewIndex - 1
      return nextReviewIndex < 0 ? currentReviewIndex : nextReviewIndex
    })
  }

  const translate = computeTranslate(rootWidth, totalCardWidth)

  return (
    <>
      <h2 css={screenReaderOnly}>Some iFit COACH Reviews</h2>
      <div ref={rootRef} css={styles.root}>
        <div css={styles.iconButtonStart} aria-hidden="true">
          <IconButton
            screenReaderLabel=""
            shape="circle"
            withBackground
            onClick={handlePreviousReview}
          >
            <Left />
          </IconButton>
        </div>
        <div css={styles.iconButtonEnd} aria-hidden="true">
          <IconButton
            screenReaderLabel=""
            shape="circle"
            withBackground
            onClick={handleNextReview}
          >
            <Right />
          </IconButton>
        </div>
        {childrenArray.map((child) => {
          return cloneElement(
            child,
            {
              ...child.props,
              width: `${cardWidth}px`,
              margin: `0 ${cardMargin}px`,
              translateX: `${translate - (totalCardWidth * currentReviewIndex)}px`
            }
          )
        })}
      </div>
    </>
  )
}

ReviewSlider.propTypes = {
  children: PropTypes.node
}

ReviewSlider.defaultProps = {
  children: null
}

export { ReviewSlider }
