import { Link } from 'gatsby'
import React from 'react'
import { useTheme } from 'emotion-theming'

import composeStyles from './styles'

const GlobalNav = () => {
  const theme = useTheme()
  const styles = composeStyles(theme)

  return (
    <nav css={styles.root}>
      <ul css={styles.list}>
        <li css={styles.listItem}><Link css={styles.link} to="/">Blog</Link></li>
        <li css={styles.listItem}><Link css={styles.link} to="/">Nourish</Link></li>
        <li css={styles.listItem}><Link css={styles.link} to="/">Shop</Link></li>
      </ul>
    </nav>
  )
}

export { GlobalNav }
