import React from 'react'
import PropTypes from 'prop-types'
import GatsbyImage from 'gatsby-image'
import { Link } from 'gatsby'
import { useTheme } from 'emotion-theming'

import { CardLink } from '../CardLink'
import composeStyles from './styles'

const EquipmentCard = ({ img, type, alt }) => {
  const theme = useTheme()
  const styles = composeStyles(theme)

  return (
    <CardLink as={Link} to="/" title={type}>
      <div css={styles.root}>
        <h3 css={styles.type}>{type}</h3>
        {img && (
          <div css={styles.imgContainer}>
            <GatsbyImage imgStyle={{ objectFit: 'scale-down' }} css={styles.img} fluid={img} alt={alt} />
          </div>
        )}
      </div>
    </CardLink>
  )
}

EquipmentCard.propTypes = {
  img: PropTypes.object,
  type: PropTypes.node,
  alt: PropTypes.string
}

EquipmentCard.defaultProps = {
  img: null,
  type: '',
  alt: ''
}

export { EquipmentCard }
