import { css } from '@emotion/core'

export default ({ colors, breakpoints }, props) => ({
  // TODO: There are some one-off colors in the comp that this button is accomodating
  // by using hard coded values. Circle back with the designer to standardize these colors
  // with the rest of the system

  root: css`
    display: flex;
    justify-content: center;
    align-items: center;
    background: ${!props.withBackground ? 'transparent' : '#000C28'};
    border: none;
    border-radius: ${props.shape === 'square' ? '0.25rem' : '50%'};
    color: ${!props.withBackground ? colors.brandSecondary : colors.lightest};
    transition: background 300ms;
    transition: box-shadow 300ms;
    width: 2.5rem;
    height: 2.5rem;
    cursor: pointer;
    ${props.withBackground ? 'opacity: 0.8;' : ''}

    &:focus {
      outline: 0.125rem solid ${colors.brand};
      outline-offset: 0.25rem;
    }

    &:hover {
      ${!props.withBackground ? 'background: #F2F2F2;' : ''}
      ${props.withBackground ? 'opacity: 0.9;' : ''}
    }

    &:active {
      box-shadow: inset 0 0 0.25rem ${!props.withBackground ? colors.brandSecondary : colors.neutral};
    }

    @media (${breakpoints.phone}) {
      padding: 0.25rem;
    }
  `
})