import React from 'react'
import { Link } from 'gatsby'

import { Layout } from '../components/Layout'
import { PageHero } from '../components/PageHero'
import { Reviews } from '../components/Reviews'
import { CardLayout } from '../components/CardLayout'
import { SeriesCard } from '../components/SeriesCard'
import { EquipmentCard } from '../components/EquipmentCard'
import { Button } from '../components/Button'
import { SEO } from '../components/SEO'

import { useSeriesQuery } from '../queries/useSeriesQuery'
import { useImagesQuery } from '../queries/useImagesQuery'
import { useEquipmentQuery } from '../queries/useEquipmentQuery'

const IndexPage = () => {
  const seriesData = useSeriesQuery()
  const imagesData = useImagesQuery()
  const equipmentData = useEquipmentQuery()

  return (
    <Layout>
      <SEO title="Home" />
      <PageHero
        text="The best personal training, right in your own home"
        action={
          <Button
            as={Link}
            to="/"
            focusColor="inverse"
            display="block"
          >
            Join iFit Coach
          </Button>
        }
      />
      <Reviews />
      <CardLayout title="Workout Series">
        {seriesData.map(({ id, avatar, hero, ...props }) => (
          <SeriesCard
            key={id}
            hero={imagesData[hero]}
            avatar={imagesData[avatar]}
            {...props} />
        ))}
      </CardLayout>
      <CardLayout title="Interested in our exciting iFit-enabled equipment?" withVisibleTitle>
        {equipmentData.map(({ id, img, ...props }) => (
          <EquipmentCard
            key={id}
            img={imagesData[img]}
            {...props}
          />
        ))}
      </CardLayout>
    </Layout>
  )
}

export default IndexPage
