import { useStaticQuery, graphql } from 'gatsby'

export const useImagesQuery = () => {
  const data = useStaticQuery(graphql`
    query ImagesQuery {
      allFile {
        nodes {
          childImageSharp {
            fluid(maxWidth: 300) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  `)
  
  return (data?.allFile?.nodes || []).reduce((result, { childImageSharp }) => {
    const fluid = childImageSharp?.fluid
    const src = fluid?.src

    return (fluid && src) ? {
      ...result,
      [src.split('/').pop()]: fluid
    } : result
  }, {})
}