import React from 'react'
import PropTypes from 'prop-types'

import { useTheme } from 'emotion-theming'

import composeStyles from './styles'

const Button = ({ as, children, href, to, focusColor, display, ...props }) => {
  const theme = useTheme()
  const styles = composeStyles(theme, {
    focusColor,
    display,
    ...props
  })

  const ElementType = as || (href || to ? 'a' : 'button')

  return (
    <ElementType
      css={styles.root}
      type={(href || to) ? null : 'button'}
      href={href}
      to={to}
      {...props}
    >
      {children}
    </ElementType>
  )
}

Button.propTypes = {
  as: PropTypes.elementType,
  children: PropTypes.node,
  href: PropTypes.string,
  to: PropTypes.string,
  focusColor: PropTypes.oneOf(['primary', 'inverse']),
  display: PropTypes.oneOf(['inline-block', 'block'])
}

Button.defaultProps = {
  as: undefined,
  children: null,
  href: undefined,
  to: undefined,
  focusColor: 'primary',
  display: 'inline-block'
}

export { Button }
