import { css } from '@emotion/core'

export default ({ colors, typography, breakpoints }, props) => ({
  root: css`
    background: ${colors.brand};
    color: ${colors.lightest};
    border-radius: 0.25rem;
    text-transform: uppercase;
    text-decoration: none;
    font-size: ${typography.fontSizeSmall};
    padding: 0.625rem 1rem;
    font-weight: 600;
    display: ${props.display};
    cursor: pointer;
    text-align: center;

    @media (${breakpoints.phone}) {
      padding: 0.3125rem 1rem;
    }

    &:focus {
      outline: 0.125rem solid ${props.focusColor === 'primary' ? colors.brand : colors.lightest};
      outline-offset: 0.25rem;
    }

    /* TODO: These should use darkening functions instead of hard coded vars */
    &:hover {
      background: #005AB4;
    }

    &:active {
      box-shadow: inset 0 0 0.5rem #001E3D;
    }
  `
})
