import React from 'react'
import { useTheme } from 'emotion-theming'

import { ReactComponent as YouTube } from '../../svg/youtube.svg'
import { ReactComponent as Pinterest } from '../../svg/pinterest.svg'
import { ReactComponent as Facebook } from '../../svg/facebook.svg'
import { ReactComponent as Twitter } from '../../svg/twitter.svg'
import { ReactComponent as Instagram } from '../../svg/instagram.svg'

import { screenReaderOnly } from '../../styles/shared'
import composeStyles from './styles'

const SocialMediaLinks = () => {
  const theme = useTheme()

  const {
    root: rootStyles,
    link: linkStyles,
    list: listStyles
  } = composeStyles(theme)

  // Providing an aria-label to supplement the links since the only content they have is an svg
  // We can disable this linting rule as a result
  /* eslint-disable jsx-a11y/anchor-has-content */
  return (
    <div css={rootStyles}>
      <p css={screenReaderOnly}>Connect with us on Social Media</p>
      <ul css={listStyles}>
        <li><a css={linkStyles} href="https://www.youtube.com/user/iFitLive" aria-label="YouTube"><YouTube aria-hidden="true" /></a></li>
        <li><a css={linkStyles} href="https://www.pinterest.com/ifit/" aria-label="Pinterest"><Pinterest aria-hidden="true" /></a></li>
        <li><a css={linkStyles} href="https://www.facebook.com/ifit" aria-label="Facebook"><Facebook aria-hidden="true" /></a></li>
        <li><a css={linkStyles} href="https://twitter.com/ifit" aria-label="Twitter"><Twitter aria-hidden="true" /></a></li>
        <li><a css={linkStyles} href="https://www.instagram.com/ifit/" aria-label="Instagram"><Instagram aria-hidden="true" /></a></li>
      </ul>
    </div>
  )
  /* eslint-enable jsx-a11y/anchor-has-content */
}

export { SocialMediaLinks }