import { useStaticQuery, graphql } from 'gatsby'

export const useSeriesQuery = () => {
  const data = useStaticQuery(graphql`
    query SeriesQuery {
      allSeriesJson {
        nodes {
          avatar
          author
          hero
          duration
          distance
          title
          workouts
          id
        }
      }
    }
  `)
  
  return data?.allSeriesJson?.nodes || []
}
