import { css, keyframes } from '@emotion/core'

const fadeIn = keyframes`
  0% {
    opacity: 0;
    transform: translate(0, -25px);
  }

  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
`

export default ({ colors, typography, breakpoints }) => ({
  root: css`
    position: relative;
  `,
  background: css`
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(0,0,0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
  `,
  content: css`
    max-width: 54rem;
    text-align: center;

    @media (${breakpoints.tablet}) {
      max-width: 38rem;
    }
  `,
  title: css`
    color: ${colors.lightest};
    font-size: ${typography.fontSizeXXLarge};
    font-weight: ${typography.fontWeightLight};
    margin-top: 0;
    margin-bottom: 3.25rem;
    line-height: 1.25;
    animation: ${fadeIn} 1.5s ease;

    @media (${breakpoints.tablet}) {
      font-size: ${typography.fontSizeXLarge};
      margin-left: 2rem;
      margin-right: 2rem;
      margin-bottom: 2rem;
    }

    @media (${breakpoints.phone}) {
      font-size: ${typography.fontSizeLarge};
      margin-left: 2rem;
      margin-right: 2rem;
      margin-bottom: 0.75rem;
    }
  `,
  action: css`
    max-width: 24.5rem;
    margin: 0 auto;

    @media (${breakpoints.tablet}) {
      max-width: 18rem;
    }

    @media (${breakpoints.phone}) {
      max-width: 10rem;
    }
  `
})