import { Link } from 'gatsby'
import React, { useState } from 'react'
import ReactDOM from 'react-dom'

import { useTheme } from 'emotion-theming'
import { useMediaQuery } from 'react-responsive'

import { Dialog } from '@instructure/ui-dialog'

import { Button} from '../Button'
import { IconButton } from '../IconButton'
import { GlobalNav } from '../GlobalNav'

import ifitLogo from '../../svg/ifit-coach-logo.svg'
import { ReactComponent as Menu } from '../../svg/menu.svg'
import { ReactComponent as Close } from '../../svg/close.svg'

import composeStyles from './styles'

const navLinks = [
  {
    label: 'Exercise',
    to: '/'
  },
  {
    label: 'Nutrition',
    to: '/'
  },
  {
    label: 'Activity',
    to: '/'
  },
  {
    label: 'Sleep',
    to: '/'
  },
]

const Header = () => {
  const theme = useTheme()
  const styles = composeStyles(theme)

  const [isOpen, setIsOpen] = useState(false)

  const handleOpen = () => {
    setIsOpen(true)
  }

  const handleClose = () => {
    setIsOpen(false)
  }

  const isTablet = useMediaQuery({ query: `(${theme.breakpoints.tablet})`})

  const renderRegisterButton = () => (
    <Button as={Link} to="/">Sign Up</Button>
  )

  const renderLogo = () => (
    <div css={styles.logoContainer}>
      <Link css={styles.logoLink} to="/"><img src={ifitLogo} alt="Ifit Coach" aria-hidden="true" /></Link>
    </div>
  )

  return (
    <header>
      <GlobalNav />
      <div css={styles.root}>
        {isTablet ? (
          <>
            {renderLogo()}
            <IconButton
              screenReaderLabel="Menu"
              onClick={handleOpen}
            >
              <Menu />
            </IconButton>
            {/* TODO: Extract this into a separate Tray component */}
            {isOpen && isTablet && ReactDOM.createPortal((
              <Dialog
                label="Menu"
                open={isOpen && isTablet}
                css={styles.mobileNav}
                onDismiss={handleClose}
                shouldContainFocus
                shouldReturnFocus
              >
                <div>
                  <IconButton
                    screenReaderLabel="Close"
                    onClick={handleClose}
                  >
                    <Close />
                  </IconButton>
                  <ul css={styles.mobileList}>
                    {navLinks.map(({ to, label }) => (
                      <li key={label} css={styles.mobileItem}>
                        <Link css={styles.listLink} to={to}>{label}</Link>
                      </li>
                    ))}
                  </ul>
                  <div css={styles.mobileRegister}>
                    {renderRegisterButton()}
                  </div>
                </div>
              </Dialog>
            ), document.body)}
          </>
        ) : (
          <>
            {renderLogo()}
            <nav css={styles.nav}>
              <ul css={styles.list}>
                {navLinks.map(({ to, label }) => (
                  <li key={label}>
                    <Link css={styles.listLink} to={to}>{label}</Link>
                  </li>
                ))}
              </ul>
            </nav>
            <div css={styles.register}>
              {renderRegisterButton()}
            </div>
          </>
        )}
      </div>
    </header>
  )
}

export { Header }
