import { css } from '@emotion/core'

export default ({ colors, shadows }) => ({
  root: css`
    display: inline-block;
    position: relative;
    background: ${colors.lightest};
    box-shadow: ${shadows.resting};
    width: 18rem;
    height: 18rem;
    border-radius: 0.25rem;
    margin: 0.75rem;
    cursor: pointer;
    transition: box-shadow 300ms;
    transition: transform 300ms;
    transform: scale(1);

    &:hover {
      box-shadow: ${shadows.above};
      transform: scale(1.0125);
    }
  `,
  link: css`
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    cursor: pointer;

    &:focus {
      outline: 0.125rem solid ${colors.brand};
      outline-offset: 0.25rem;
    }
  `
})