import React from 'react'
import PropTypes from 'prop-types'
import { useTheme } from 'emotion-theming'

import { screenReaderOnly } from '../../styles/shared'

import composeStyles from './styles'

const CardLayout = ({ children, title, withVisibleTitle }) => {
  const theme = useTheme()
  const styles = composeStyles(theme)

  return (
    <div css={styles.root}>
      <h2 css={withVisibleTitle ? styles.title : screenReaderOnly}>{title}</h2>
      <div css={styles.content}>
        <div css={styles.cards}>
          {children}
        </div>
      </div>
    </div>
  )
}

CardLayout.propTypes = {
  title: PropTypes.string,
  children: PropTypes.node,
  withVisibleTitle: PropTypes.bool
}

CardLayout.defaultProps = {
  title: '',
  children: null,
  withVisibleTitle: false
}

export { CardLayout }
