import React from 'react'
import PropTypes from 'prop-types'
import { useTheme } from 'emotion-theming'

import { screenReaderOnly } from '../../styles/shared'

import composeStyles from './styles'

const Review = ({ children, logo, citation, citationUrl, width, margin, translateX }) => {
  const theme = useTheme()
  const styles = composeStyles(theme, {
    width,
    margin,
    translateX
  })

  return (
    <div css={styles.root}>
      {logo && <div css={styles.logo} aria-hidden="true">{logo}</div>}
      <blockquote css={styles.blockquote} cite={citationUrl}>
        "{children}"
        <cite css={screenReaderOnly}>{citation}</cite>
      </blockquote>
    </div>
  )
}

Review.propTypes = {
  logo: PropTypes.node,
  children: PropTypes.node,
  citation: PropTypes.string,
  citationUrl: PropTypes.string,
  width: PropTypes.string,
  margin: PropTypes.string,
  translateX: PropTypes.string
}

Review.defaultProps = {
  logo: null,
  children: null,
  citation: '',
  citationUrl: '',
  // For simplicity we'll use pixels. We could also use the root element to convert relative unit measures
  // (em/rem) and have this function for users with a different default font
  width: '440px',
  margin: '0 12px',
  translateX: '0px'
}

export { Review }
