import React, { useState } from 'react'
import PropTypes from 'prop-types'
import Duration from 'luxon/src/duration'
import GatsbyImage from 'gatsby-image'
import { Link } from 'gatsby'
import { useTheme } from 'emotion-theming'

import { ReactComponent as Playlist } from '../../svg/playlist.svg'
import { ReactComponent as Time } from '../../svg/time.svg'
import { ReactComponent as Distance } from '../../svg/distance.svg'

import { CardLink } from '../CardLink'

import composeStyles from './styles'

/* eslint-disable jsx-a11y/mouse-events-have-key-events */
const SeriesCard = ({ avatar, author, hero, duration, distance, title, workouts }) => {
  const theme = useTheme()
  const styles = composeStyles(theme)

  const { hours, minutes } = (Duration.fromISO(duration)?.values || {})

  const [isMouseOver, setIsMouseOver] = useState(false)

  const handleMouseOver = () => {
    setIsMouseOver(true)
  }

  const handleMouseOut = () => {
    setIsMouseOver(false)
  }

  return (
    <CardLink
      as={Link}
      to="/"
      title={title}
      onMouseOver={handleMouseOver}
      onMouseOut={handleMouseOut}
    >
      {hero && (
        <div css={styles.hero}>
          <GatsbyImage fluid={hero} alt={title} />
          {workouts && (
            <div css={styles.workouts}>
              <div>
                <div css={styles.workoutsCount}>{workouts}</div>
                <div css={styles.workoutsText}>workouts</div>
                <Playlist css={styles.playlist} />
              </div>
            </div>
          )}
        </div>
      )}
      <div css={styles.content}>
        <div css={styles.details}>
          <h3 css={styles.title}>{title}</h3>
          <div css={styles.detailList}>
            {duration && (
              <div css={styles.detail}>
                <div css={styles.detailIcon}>
                  <Time />
                </div>
                <time dateTime={duration}>{hours}:{minutes}</time>
              </div>
            )}
            {distance && (
              <div css={styles.detail}>
                <div css={styles.detailIcon}>
                  <Distance />
                </div>
                {distance.toLocaleString()} M
              </div>
            )}
          </div>
        </div>
        {avatar && (
          <div css={styles.avatar}>
            <GatsbyImage css={styles.img} fluid={avatar} alt={author} />
          </div>
        )}
      </div>
      {isMouseOver && <p css={styles.viewDetails}>View Details</p>}
    </CardLink>
  )
}

SeriesCard.propTypes = {
  avatar: PropTypes.object,
  hero: PropTypes.object,
  author: PropTypes.string,
  duration: PropTypes.string,
  distance: PropTypes.number,
  title: PropTypes.node,
  workouts: PropTypes.node
}

SeriesCard.defaultProps = {
  avatar: null,
  hero: null,
  author: '',
  duration: '',
  distance: null,
  title: null,
  workouts: null
}

export { SeriesCard }